<template>
  <view
    class="bg"
    :class="[audioPlay ? 'voicePlay' :'']"
    @click="play"
  >
  </view>
</template>

<script>
export default {
  name: 'playWords',
  components: {},
  mixins: [],
  data() {
    return {
      innerAudioContext: null,
      timeID: null
    }
  },
  props: {
    audioLink: {
      type: String,
      default: 'https://www.runoob.com/try/demo_source/horse.mp3'
    },
    playId: {
      type: String,
      default: 'audio'
    },
    audioPlay: {
      type: Boolean,
      default: false
    },
    firstLoad: {
      type: Boolean,
      default: false
    },
  },
  created() {
    this.creatAudio()
    this.autoLoopPlay()
  },

  mounted() {

  },
  beforeDestroy() {
    this.destroy()
  },

  computed: {},


  methods: {
    //录音实例
    creatAudio() {
      this.innerAudioContext = uni.createInnerAudioContext();//创建实例
      this.innerAudioContext.autoplay = true;//设置是否自动播放
      // this.innerAudioContext.loop = true;
      this.innerAudioContext.src = this.audioLink

      this.innerAudioContext.onCanplay(() => {
        if (this.audioPlay) {
          this.pause()
        } else {
          this.play()
        }
      })

      this.innerAudioContext.onEnded(() => {
        this.$emit('update:audioPlay', false)
      })
    },
    // 结束
    pause() {
      // this.innerAudioContext.loop = false;
      this.$emit('update:audioPlay', false)
      this.innerAudioContext.pause()
    },
    // 播放
    play() {
      this.innerAudioContext.src = this.audioLink
      this.$emit('update:audioPlay', true)
      this.innerAudioContext.play(); //直接播放
    },
    destroy() {
      this.innerAudioContext.destroy()
      clearTimeout(this.timeID)
    },
    autoLoopPlay() {
      if (this.timeID) {
        clearTimeout(this.timeID); //   清除定时器
      }
      this.timeID = setInterval(() => {
        //   定时器
        if (this.firstLoad) {
          this.creatAudio()
        }
      }, 1000);
    },
  }
}

</script>
<style lang='scss' scoped>
.bg {
  background: url(data:image/png;base64,)
    right 0 no-repeat;
  width: 24rpx;
  height: 24rpx;
  background-size: 400%;
}

.voicePlay {
  animation-name: voicePlay;
  animation-duration: 1s;
  animation-direction: normal;
  animation-iteration-count: infinite;
  animation-timing-function: steps(3);
}

@keyframes voicePlay {
  0% {
    background-position: 0;
  }
  100% {
    background-position: 100%;
  }
}
</style>